svg, a {
	transition: 0.3s all;
}

svg, img {
	display: block;
}

.icon-svg {
	height: 1rem;
	width: 1rem;
}

i {
	font-size: inherit;
	color: inherit;
}

.body {
	font-family: $font-main;
	font-size: 16px;
	line-height: 1.5;
	overflow-x: hidden;
	background-color: $light;
	color: $dark;
	background: url(../img/bg-svg.svg) 0px 140px no-repeat, 
                url(../img/bg-svg.svg) 0px 1640px no-repeat,
                url(../img/bg-svg.svg) 0px 3140px no-repeat,
                url(../img/bg-svg.svg) 0px 4640px no-repeat,
                url(../img/bg-large-svg.svg) right 50px no-repeat,
                url(../img/bg-large-svg.svg) right 3250px no-repeat,
                ;
	@include r(1700) {

	}
	@include r(1200) {

	}
}